import React, {Component} from 'react'

export default class PageSection extends Component{
    constructor(props){
        super(props);

        this.state = {
            color : props.color ? props.color : "#000",
            bg : props.bg ? props.bg : "#fff",
            type : props.type ? props.type : 0,
            content : props.content ? props.content : <div>Your content here</div>
        }
    }

    render() {

        const {color, bg, type, content} = this.state;

        switch(type){
            default:
                return (
                    <section className="page-section" id="contact" 
                    style={{
                        background: bg,
                        color: color
                    }}>
                        {content}
                    </section>
                );
            case 1:
                return (
                    <section className="page-section portfolio" id="portfolio" 
                    style={{
                        background: bg,
                        color: color
                    }}>
                        {content}
                    </section>
                );
                case 2:
                    return (
                        <section className="page-section" id="about" 
                        style={{
                            background: bg,
                            color: color
                        }}>
                            {content}
                        </section>
                    );
        }
    }
}