import React from 'react'

import Devider from '../divider/Devider'
import GridElement from '../grid-element/GridElement'
import TitleSection from '../title-section/TitleSection'
import Container from '../container/Container'
import Row from '../row/Row'
import PageSection from '../page-section/PageSection'


export default class GridInfo extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            color: props.color
        }
    }

    render(){

        const {color} = this.state;

        const rowContent =[
                <GridElement/>,
                <GridElement/>,
                <GridElement/>,
                <GridElement/>,
                <GridElement/>,
                <GridElement/>,
        ]; 
        const content = [
            <TitleSection name="Clínica" color="#000"/>,
            <Devider/>,
            <Row content={rowContent}/>
            
        ];

        if(color){
            return(
                <section class="page-section portfolio" id="portfolio" style={{background: color}}>
                    <Container content={content}/>
                </section>
            );
        }else{
            return(
                <section class="page-section portfolio" id="portfolio">
                    <Container content={content}/>
                </section>
            );
        }
    }
}